.insight-info {
  width: 100%;
  padding: 0 0 0 15px;
  margin-bottom: 82px;
  margin-top: -21px;
}

.insight-info__content {
  border-collapse: separate;
  border-spacing: 10px 10px;
  width: 100%;
}

.insight-info__cell {
  background-color: #fff;
  text-align: center;
  max-height: 60px;
}

.insight-info__cell--for-text {
  padding: 0 10px;
  line-height: 60px;
}

.insight-info__cell--for-icons {
  background: none;
  width: 15px;
}

.insight-info__cell--id {
  color: rgba(34, 34, 34, 0.5);
  width: 5%;
}

.insight-info__cell--for-input {
  min-width: 120px;
}

.insight-info__cell--scroll {
  overflow-y: auto;
}

.insight-info__cell--white-text {
  color: #fff;
}

.insight-info__cell--category {
  min-width: 100px;
}

.insight-info__cell--tag {
  min-width: 100px;
}

.insight-info__cell--white-text .Select-arrow {
  border-color: #fff transparent transparent;
}

.insight-info__insight-icon {
  height: 25px;
}

.insight-info__group-cell {
  padding: 10px 20px;
  text-align: left;
  border: 0;
  border-radius: 0;
  height: 100%;
  width: 100%;
  font-weight: inherit;
  background: none !important;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  color: inherit !important;
}

.insight-info__caret-icon {
  float: right;
}

@media only screen and (max-width: 730px) {
  .insight-info {
    display: block;
    padding: 0 15px 0 0;
    width: 100%;
    overflow-x: auto;
    overflow-y: visible;
    /* height: 100%; */
    margin-bottom: 65px;
  }
  .insight-info__content {
    padding-bottom: 30px;
  }
}



