.thunder-display {
  text-align: center;
  /* margin-top: 0.5%; */
  margin-bottom: 80px;
}

.thunder-display .stepper {
  /* margin-top: 1%; */
  margin-left: auto;
  margin-right: auto;
  /* width: 828px; */
}

.thunder-display__tabs {
  margin-top: 2.5%;
}

.thunder-display__tabs .nav-tabs {
  border: none;
  padding: 0% 16%;
}

.thunder-display__text-1 {
  font-family: Roboto;
  font-size: 28.8px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #222222;
  margin-top: 2.5%;
  margin-bottom: 2.4%;
}

.thunder-display__tabs-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 1%;
}

.thunder-display__tab-title {
  font-family: Roboto;
  font-size: 28px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  width: 50%;
}

.thunder-display__tab-title .active a {
  color: #0070c0;
}

.nav-tabs > li.thunder-display__tab-title,
.nav-tabs > li.thunder-display__tab-title:focus,
.nav-tabs > li.thunder-display__tab-title:hover {
  font-weight: normal;
  color: #222222;
}

.nav-tabs > li.thunder-display__tab-title.active,
.nav-tabs > li.thunder-display__tab-title.active:focus,
.nav-tabs > li.thunder-display__tab-title.active:hover {
  font-weight: bold;
  color: #0070c0;
}

.nav-tabs > li.thunder-display__tab-title > a,
.nav-tabs > li.thunder-display__tab-title > a:focus,
.nav-tabs > li.thunder-display__tab-title > a:hover {
  font-weight: normal;
  color: #222222;
  border: none;
}

.nav-tabs > li.thunder-display__tab-title.active > a,
.nav-tabs > li.thunder-display__tab-title.active > a:focus,
.nav-tabs > li.thunder-display__tab-title.active > a:hover {
  font-weight: bold;
  color: #0070c0;
  background-color: transparent;
  border: none;
  border-bottom: 4px solid #0070c0;
}

.thunder-display__tabs .row {
  margin-top: 1%;
}

.thunder-display__category-color,
.thunder-display__category-color:focus {
  padding: 0%;
  width: 91px;
}

.thunder-display__category-color-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.thunder-display__category-color {
  width: 64px;
  height: 64px;
}

.thunder-display__category-color-drop {
  width: 22px;
  height: 30px;
  margin-top: 16px;
}

.thunder-display__drag-me {
  margin-left: 10px;
}

.thunder-display__button--add,
.thunder-display__button--add:focus {
  opacity: 0.5;
  font-family: Roboto;
  font-size: 30px;
  margin-top: 7%;
}

.thunder-display__input--select .Select-control {
  height: 0px;
  padding: 0px;
  border: 0;
}

.thunder-display__input--select .Select-placeholder {
  color: #222222;
  opacity: 0.5;
}

.thunder-display__input--select .Select-value-label {
  padding: 0px;
}



