.graph-layout {
  width: 100%;
  height: 100%;
  overflow: auto;
}

.graph-layout__impact-block {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.graph-layout__impact-icon {
  min-width: 32px;
  width: 14px;
  margin: 0 5px 0 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-weight: 800;
  font-size: var(--font-size-large);
  padding: 3.5px;
}

.graph-layout__graph-container {
  border-right: 1.5px solid rgba(0, 0, 0, 0.2);
}

.graph-layout__graph-container:not(.graph-layout__graph-container--neg) {
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    color-stop(60%, rgba(0, 0, 0, 0.2)),
    color-stop(0%, rgba(255, 255, 255, 0))
  );
  background-image: -webkit-linear-gradient(
    left,
    rgba(0, 0, 0, 0.2) 60%,
    rgba(255, 255, 255, 0) 0%
  );
  background-image: -o-linear-gradient(
    left,
    rgba(0, 0, 0, 0.2) 60%,
    rgba(255, 255, 255, 0) 0%
  );
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.2) 60%,
    rgba(255, 255, 255, 0) 0%
  );
  background-position: bottom;
  background-size: 10px 1px;
  background-repeat: repeat-x;
}

.graph-layout__graph-container--neg {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.graph-layout__graph-container--graph-pos-0,
.graph-layout__graph-container--graph-zero-0,
.graph-layout__graph-container--graph-neg-0 {
  border-left: 1.5px solid rgba(0, 0, 0, 0.2);
}



