.fileselector {
  width: 100%;
  height: 100px;
  background-color: #029af782;
  border-radius: 23px;
  margin-top: 1%;
}

.fileselector p {
  padding-top: 35px;
}

#filepicker {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100px;
  outline: none;
  opacity: 0;
}

/* LIST */

/* ================== */

.files-dropzone-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 10px;
  margin: 10px;
  border: 1px dashed #d3d3d3;
  cursor: pointer;
}

.files-buttons {
  border-top: 1px solid #d3d3d3;
  cursor: pointer;
}

.files-button-submit {
  display: inline-block;
  text-align: center;
  height: 40px;
  line-height: 40px;
  width: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-right: 1px solid #d3d3d3;
}

.files-button-submit:before {
  content: 'Submit';
}

.files-button-clear {
  display: inline-block;
  text-align: center;
  height: 40px;
  line-height: 40px;
  width: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.files-button-clear:before {
  content: 'Clear';
}

.files-list {
  /* width: 300px; */
}

.files-list ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.files-list li:last-child {
  border: none;
}

.files-list-item {
  height: 60px;
  padding: 10px 0px 10px 10px;
  /*border-bottom: 1px solid #D3D3D3;*/
}

.files-list-item-content {
  float: left;
  padding-top: 5px;
  padding-left: 10px;
  width: calc(100% - 130px);
  height: 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.files-list-item-content-item {
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.files-list-item-content-item-1 {
  font-size: 20px;
  line-height: 30px;
}

.files-list-item-content-item-2 {
  font-size: 16px;
  line-height: 20px;
}

.files-list-item-preview {
  height: 60px;
  width: 60px;
  float: left;
}

.files-list-item-preview-image {
  height: 100%;
  width: 100%;
}

.files-list-item-preview-extension {
  text-align: center;
  line-height: 60px;
  color: #fff;
  background-color: #d3d3d3;
  font-size: 16px;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  padding-left: 5px;
  padding-right: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.files-list-item-remove {
  height: 60px;
  width: 60px;
  float: right;
  cursor: pointer;
  background: url(data:image/svg+xml;base64,)
    no-repeat center center;
  background-size: 30px 30px;
}

.files-list-item-remove-image {
  height: 30px;
  width: 30px;
  margin-top: 15px;
  margin-right: 10px;
  float: right;
}

.react-checkbox-tree {
  margin-top: 3.5%;
  margin-left: 3%;
}

.react-checkbox-tree label {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.react-checkbox-tree ol ol {
  padding-left: 39px;
  padding-top: 6px;
}

.rct-collapse.rct-collapse-btn {
  display: none;
}

.rct-icon.rct-icon-uncheck,
.rct-icon.rct-icon-half-check {
  display: inline-block;
  text-indent: -9999px;
  width: 20px;
  height: 20px;
  background: url('../../../assets/images/ckeck-0.svg');
  background-size: 20px 20px;
  margin-right: 7px;
}

.rct-icon.rct-icon-check {
  display: inline-block;
  text-indent: -9999px;
  width: 20px;
  height: 20px;
  background: url('../../../assets/images/check-icon.svg');
  background-size: 20px 20px;
  margin-right: 7px;
}

.rct-node-parent .rct-node-icon {
  display: inline-block;
  text-indent: -9999px;
  width: 36px;
  height: 26px;
  background: url('../../../assets/images/folder.svg');
  background-size: 36px 26px;
}

.rct-node-leaf .rct-node-icon {
  display: inline-block;
  text-indent: -9999px;
  width: 36px;
  height: 26px;
  background: url('../../../assets/images/folder-node.svg');
  background-size: 36px 26px;
}

.rct-title {
  font-family: Roboto;
  font-size: 35px;
  font-weight: normal;
  line-height: normal;
  color: #222222;
}



