.app-input,
.app-input:focus {
  outline: none;
  border: none;
  background-color: white;
  padding: 9px;
  width: 100%;
  height: 64px;
  text-align: center;
  font-family: Roboto;
  font-size: 20px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #222222;
}

.center-input {
  text-align: center;
  text-align-last: center;
}



