.rectangle-item {
  width: 165px;
  height: 200px;
  padding: 8px 0px;
  margin-right: 2%;
}

.rectangle-item__image {
  width: 118px;
  height: 118px;
  margin-left: auto;
  margin-right: auto;
}

.rectangle-item__img {
  max-width: 110px;
  max-height: 110px;
}

.rectangle-item__title {
  font-family: Roboto;
  font-size: 21.6px;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #222222;
}

.rectangle-item__create-date {
  margin-top: 14px;
  opacity: 0.5;
  font-family: Roboto;
  font-size: 12.6px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #222222;
}



