.data-table-input {
  width: 100%;
  height: 100%;
  border-radius: 0;
  background: none;
  color: inherit;
  text-align: center;
  padding-left: 0;
  padding-right: 0;
}

.data-table-input,
.data-table-input:hover,
.data-table-input:focus,
.data-table-input:active {
  outline: 0;
  outline-offset: 0;
  border: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  padding: 0 10px;
}

.data-table-textarea__wrapper {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
}

.data-table-textarea {
  border-radius: 0;
  background: none;
  color: inherit;
  text-align: center;
  padding-left: 0;
  padding-right: 0;
  resize: none;
}

.data-table-textarea,
.data-table-textarea:hover,
.data-table-textarea:focus,
.data-table-textarea:active {
  outline: 0;
  outline-offset: 0;
  border: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  padding: 0 10px;
  max-height: 60px;
}



