.dashboard {
  margin-left: 10%;
  margin-right: 10%;
}

.dashboard__welcome-message {
  /* width: 797.4px; */
  height: 40.8px;
  font-family: Roboto;
  font-size: 28.8px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #222222;
}

.dashboard__thunders-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  margin-top: 2%;
  margin-bottom: 6%;
}

.dashboard__thunders-wrapper a {
  text-decoration: none;
}

.dashboard__logo {
  margin-bottom: 3%;
  margin-top: -35px;
}



