.stepper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 720px;
  margin-bottom: 40px;
}

.stepper__step-wrapper {
  width: 67px;
  height: 67px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0 20px;
}

.stepper__icon {
  width: 57px;
  height: 57px;
  z-index: 10;
}

.stepper__icon-wrapper {
  width: 67px;
  cursor: pointer;
}

.stepper__icon-wrapper--empty {
  padding: 5px;
}

.stepper__icon-wrapper--selected {
  border: 2px solid #0070c0;
  border-radius: 40px;
  padding: 3px;
}

.stepper__divider {
  height: 1px;
  width: 260px;
  border-bottom: 2px dashed #a4a4a4;
}

.stepper__title {
  position: relative;
  text-align: center;
  font-weight: bold;
  color: #222222;
  font-size: var(--font-size-medium-2);
  margin-top: 5px;
}



