.thunder {
  margin: 10px 0px;
}

.thunder__title {
  font-family: Roboto;
  font-size: 21.6px;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #222222;
  margin-top: 16px;
}

.thunder__date {
  height: 14.5px;
  opacity: 0.5;
  font-family: Roboto;
  font-size: 12.6px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #222222;
  margin-top: 11px;
}

.thunder__description--container,
.thunder__description--container a {
  width: 156px;
  height: 213.6px;
  border-radius: 4.8px;
  background-color: #0070c0;
  -webkit-box-shadow: 0 6px 24px 0 rgba(0, 112, 192, 0.4);
          box-shadow: 0 6px 24px 0 rgba(0, 112, 192, 0.4);
  text-align: center;
  color: #ffffff;
  font-family: Roboto;
  letter-spacing: normal;
  margin: 0px 22px;
}

.thunder__description--content {
  height: 185px;
  overflow: hidden;
}

.thunder__description--title {
  font-size: 21.6px;
  font-weight: 900;
  margin-top: 8px;
}

.thunder__description--date {
  font-size: 12.6px;
  font-weight: 500;
  padding: 3px 16px;
}

.thunder__description--text {
  font-size: 12.6px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.24;
  padding: 4px 11px;
  text-align: left;
  /* max-height: 108px; */
  overflow-y: hidden;
}

.thunder__options {
  font-size: 12.6px;
  font-weight: 500;
  border-top: 0.6px #ffffff9e solid;
  margin: 0px 12px;
  padding-top: 2px;
}

.thunder__options--white {
  background-color: #f0f0f0;
  font-weight: bold;
  color: #0070c0;
  margin: 0px;
  padding-bottom: 8px;
  border-radius: 0px 0px 4.8px 4.8px;
}

.thunder__description--menu-item {
  font-size: 12.6px;
  font-weight: 500;
  margin: 12px 0px;
}

@page {
  size: 65cm 15.7cm;
  margin: 5mm 5mm 5mm 5mm;
}



