.datasource-popup {
}

.datasource-popup__img {
  max-width: 30px;
  max-height: 30px;
}

.datasource-popup__search,
.datasource-popup__search:focus {
  width: 220.4px;
  height: 30px;
  border-radius: 15px;
  background-color: #f0f0f0;
  border: none;
  text-align: center;
}

.datasource-popup__text,
.datasource-popup__image {
  display: inline;
}

.datasource-popup__text {
  margin-left: 16px;
  font-family: Roboto;
  font-size: 18px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #222222;
}

#popover-datasorce {
  margin-top: -127px;
  width: 268.6px;
  height: 448px;
  background-color: #ffffff;
  -webkit-box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.2);
  border: none;
  border-radius: 0;
  padding: 10px 2px;
}

.datasource-popup__list {
  margin-top: 28px;
  overflow-y: scroll;
  max-height: 366px;
}

.datasource-popup__list-item {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 26px;
}



