.instance-table-container {
  margin-bottom: 64px;
  margin-top: -6px;
}

.instance-table {
  border-spacing: 12px;
}

.instance-table > thead > tr > th {
  font-family: Roboto;
  font-size: 18px;
  color: #222222;
  padding: 9px 5px 22px 5px;
  line-height: 32px;
  font-weight: normal;
  border-bottom: none;
}
.instance-table > thead > tr > th:last-child {
  padding: 9px 15px 22px 5px;
}

.instance-table > thead > tr > th > div {
  background-color: #cfcfcf;
  padding-left: 22px;
  padding-right: 10px;
}

.instance-table > thead > tr > th:first-child {
  background-color: rgb(240, 240, 240);
  vertical-align: middle;
  text-align: center;
  line-height: 37px;
  border-bottom: none;
  padding: 0px 0px 10px 20px;
}

.instance-table > thead > tr > th:first-child > div {
  width: 35.4px;
  height: 36px;
  border-radius: 17.7px;
  background-color: #cfcfcf;
  padding: 0px;
}

.instance-table__spacer {
  height: 0px;
}

.instance-table > tbody > tr > td:first-child {
  vertical-align: middle;
  text-align: center;
  line-height: 37px;
  padding: 0px 0px 0px 17px;
}

.instance-table > tbody > tr > td:first-child .instance-table__wrapper--div {
  height: 78px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 0;
}
.instance-table > tbody > tr > td:last-child .instance-table__wrapper--div {
  padding-right: 0;
}
.instance-table
  > tbody
  > tr
  > td:first-child
  .instance-table__wrapper--div
  div {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #fff;
  overflow: hidden;
}

.instance-table > tbody {
  font-family: Roboto;
  font-size: 18px;
  font-weight: 100;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #222222;
  text-align: center;
}

.instance-table > tbody > tr > td .instance-table__wrapper--div div {
  background-color: #fff;
  height: 72px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  overflow-y: auto;
}

.instance-table > tbody > tr > td {
  border: none;
  vertical-align: middle;
  position: relative;
  padding: 0;
}

.instance-table__record {
  height: 84px;
  /* border-color: #f0f0f000; */
}

.instance-table__record--selected {
  /*  border: solid 3.6px;
  border-color: #0070c0;*/
}

.instance-table__record--column {
  width: 250px;
}

.instance-table__record--selected > td:before {
  /*  content: '';
  background-color: rgba(0, 112, 192, 0.1);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;*/
}

.instance-table__record--grayscale {
  -webkit-filter: grayscale(100%);
  /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
}

.instance-table__img--datasource {
  height: 43px;
  margin: 6px;
}

.instance-table tbody td.instance-table__column--context > div {
  -ms-flex-pack: start;
      justify-content: start;
}

.instance-table__column--context .instance-table__wrapper--div div {
  font-family: Roboto;
  font-size: 18px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #222;
  max-height: 72px;
}
.instance-table__wrapper--div {
  padding: 0 5px;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
  position: relative;
}
.instance-table td:first-child .instance-table__wrapper--div {
  border-left: 3px solid transparent;
}
.instance-table td:last-child .instance-table__wrapper--div {
  border-right: 3px solid transparent;
  margin: 0 12px 0 0;
}

.instance-table__record--selected .instance-table__wrapper--div::before {
  content: '';
  background-color: rgba(0, 112, 192, 0.1);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.instance-table__record--selected td:first-child .instance-table__wrapper--div {
  border-left: 3px solid #0070c0;
}
.instance-table__record--selected td:last-child .instance-table__wrapper--div {
  border-right: 3px solid #0070c0;
}

.instance-table__record--selected .instance-table__wrapper--div {
  border-top: 3px solid #0070c0;
  border-bottom: 3px solid #0070c0;
}

.instance-table__column--text {
  max-height: 72px;
}

mark {
  background-color: #f8e71c;
}



